import { Component, OnInit,  Output, EventEmitter  } from '@angular/core';
import { SptModel } from '../spt-model';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { TreatmentHistoryModel } from '../treatment-history-model';
import { environment } from '../../environments/environment';


@Component({
  selector: 'app-add-spt',
  templateUrl: './add-spt.component.html',
  styleUrls: ['./add-spt.component.css']
})
export class AddSptComponent implements OnInit {

  public sptData : SptModel;
  public message : string;
  public sptId : any;
  public treatmentHistory: TreatmentHistoryModel;


  public isLoading: boolean;
  @Output("closePopup") closePopup = new EventEmitter();


  constructor(private tService : TreatmenthistoryService) { 
    this.sptData = new SptModel();
  }

  ngOnInit() {
    
  }

  dateMaker(dateObject){
    let stringDate = dateObject.day + "-" + dateObject.month + "-" + dateObject.year; 
    return stringDate;
  }

  savesptData(){
    this.message = "";
    let data = this.sptData;
    data.trtmtBookedDt = this.dateMaker(this.sptData.treatmentBookDate);
    data.trtmtStartDt = this.dateMaker(this.sptData.treatmentStartDate);
    data.addedBy = Number.parseInt(localStorage.getItem("userId"));
    data.patientId = Number.parseInt(localStorage.getItem("patientId"));

    this.tService.addSPT(data).subscribe(result => {
      this.sptId = result;
      this.message = environment.TREATMENT_SPT_ADD_SUCCESS;
    });
  }

  editsptData(){
    this.message = "";
    let data = this.sptData;
    data.trtmtStartDt = this.dateMaker(this.sptData.treatmentStartDate);
    data.trtmtBookedDt = this.dateMaker(this.sptData.trtmtBookedDt);
    data.addedBy = Number.parseInt(localStorage.getItem("userId"));
    data.patientId = Number.parseInt(localStorage.getItem("patientId"));
    data.speechTherapyId = this.sptId;
    this.tService.editSPT(data).subscribe(result => {
      this.sptId = result;
      this.message = environment.TREATMENT_SPT_EDIT_SUCCESS;
    });
  }

  closeInter() {
    this.closePopup.emit("true");
  }

}
