import { Component, OnInit, ViewChild, ElementRef, OnDestroy } from '@angular/core';
import { AuthService } from './auth.service';
import { Subscription } from 'rxjs/Subscription';
import { Router } from '@angular/router';
import { ModalService } from './modal.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {
  ngOnDestroy(): void {
    this.authSubscription.unsubscribe();
    this.modalSubscription.unsubscribe();
  }
  public isAuth: boolean = false;
  public title = 'app';
  public authSubscription: Subscription;
  public modalSubscription: Subscription;
  public userName = "";
  public modalData: any;
  public roles: string;
  public backgroundSrc :string [] = [];
  public accesss: any = [];
  @ViewChild("SideNav") SideNav: ElementRef;
  @ViewChild("commonModal") commonModal;

  constructor(private authService: AuthService,
    private modalService: ModalService,
    public router: Router) {
    this.modalData = this.modalService.defaut;
  }

  ngOnInit() {
    this.authSubscription = this.authService.getAuth().subscribe((status: any) => {
      this.isAuth = status.isAuth;
      this.userName = status.userName;
      console.log(JSON.stringify(status));
      this.accesss = status.access;
      console.log(status);
      this.roles = status.role && status.role.toString();
      this.backgroundSrc.push('../assets/images/1.jpg');
      this.backgroundSrc.push('../assets/images/2.jpg');
      this.backgroundSrc.push('../assets/images/3.jpg');
      if (!status.isAuth)
        this.router.navigate(["login"], { replaceUrl: true });
    })

    this.modalSubscription = this.modalService.getModal().subscribe((status: any) => {
      localStorage.setItem("selectedPatient", "");
      this.modalData = status || this.modalService.defaut;
    })
  }

  openNav() {
    this.SideNav.nativeElement.style.width = "250px";
  }

  closeNav() {
    this.SideNav.nativeElement.style.width = "0px";
  }

  logout() {
    localStorage.setItem("token", "");
    this.authService.setAuth({ isAuth: false });
  }

  closeModal() {
    this.modalService.setModal(this.modalService.defaut);
  }

}
