import { Component, OnInit , Output, EventEmitter  } from '@angular/core';

import { TransportationModel } from '../transportation-model';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { TreatmentHistoryModel } from '../treatment-history-model';
import { environment } from '../../environments/environment';

@Component({
  selector: 'app-add-transportation',
  templateUrl: './add-transportation.component.html',
  styleUrls: ['./add-transportation.component.css']
})
export class AddTransportationComponent implements OnInit {

  public transData : TransportationModel;
  public message : string;
  public transId : any;
  public treatmentHistory: TreatmentHistoryModel;


  public isLoading: boolean;
  @Output("closePopup") closePopup = new EventEmitter();


  constructor(private tService : TreatmenthistoryService) { 
    this.transData = new TransportationModel();
  }

  ngOnInit() {
    
  }

  dateMaker(dateObject){
    let stringDate = dateObject.day + "-" + dateObject.month + "-" + dateObject.year; 
    return stringDate;
  }

  saveTransData(){
    this.message = "";
    let data = this.transData;
    data.transToCentreDt = this.dateMaker(this.transData.toDate);
    data.transToHomeDt = this.dateMaker(this.transData.fromDate);
    data.addedBy = Number.parseInt(localStorage.getItem("userId"));
    data.patientId = Number.parseInt(localStorage.getItem("patientId"));

    this.tService.addTransport(data).subscribe(result => {
      this.transId = result;
      this.message = environment.TREATMENT_TRANS_ADD_SUCCESS;
    });
  }

  editTransData(){
    this.message = "";
    let data = this.transData;
    data.transToCentreDt = this.dateMaker(this.transData.toDate);
    data.transToHomeDt = this.dateMaker(this.transData.fromDate);
    data.addedBy = Number.parseInt(localStorage.getItem("userId"));
    data.patientId = Number.parseInt(localStorage.getItem("patientId"));
    data.transId = this.transId;
    this.tService.editTransport(this.transId,data).subscribe(result => {
      this.transId = result;
      this.message = environment.TREATMENT_TRANS_EDIT_SUCCESS;
    });
  }

  closeInter() {
    this.closePopup.emit("true");
  }
}
