import { Component, OnInit , Output, EventEmitter   } from '@angular/core';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { InterventionModel } from '../intervention-model';
import { environment } from '../../environments/environment'
import { DoctorService } from '../service/doctor.service';

@Component({
  selector: 'app-show-double-interventions',
  templateUrl: './show-double-interventions.component.html',
  styleUrls: ['./show-double-interventions.component.css']
})
export class ShowDoubleInterventionsComponent implements OnInit {
  public items: InterventionModel[];
  public doctors:[];


  public message: string;
  public isLoading: boolean;
  @Output("closePopup") closePopup = new EventEmitter();

  constructor(private tService : TreatmenthistoryService,  private dService : DoctorService) { }

  ngOnInit() {
    let patientId = localStorage.getItem("patientId");
    this.getData(patientId);
  }
  getData(patientId: string): any {
    this.tService.getDoubleInterventionHistoryForPatient(patientId).subscribe(success =>{
      this.items = success;
      console.log(success)
    }, error => {
      console.log(environment.ERROR_SOMETHING_WENT_WRONG);
    });
  }
}
