import { Component, OnInit, Output, EventEmitter  } from '@angular/core';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { InterventionModel } from '../intervention-model';
import { environment } from '../../environments/environment'

@Component({
  selector: 'app-show-interventions',
  templateUrl: './show-interventions.component.html',
  styleUrls: ['./show-interventions.component.css']
})
export class ShowInterventionsComponent implements OnInit {

  public items: InterventionModel[];

  public message: string;
  public isLoading: boolean;
  @Output("closePopup") closePopup = new EventEmitter();

  constructor(private tService : TreatmenthistoryService) { }

  ngOnInit() {
    let patientId = localStorage.getItem("patientId");
    // this.treatmentHistory = new TreatmentHistoryModel();
    this.getData(patientId);
  }
  getData(patientId: string): any {
    this.tService.getSingleInterventionHistoryForPatient(patientId).subscribe(success =>{
      this.items = success;
      console.log(success)
    }, error => {
      console.log(environment.ERROR_SOMETHING_WENT_WRONG);
    });
  }
}
