import { Injectable } from '@angular/core';
import { HttpHelperService } from '../http-helper.service';
import { environment } from '../../environments/environment';

@Injectable()
export class AddPatientService {

  constructor(public http: HttpHelperService) { }

  addPatient(requestBody) {
    return this.http.post(environment.API_ADD_PATIENT, requestBody);
  }

  editPatient(requestBody) {
    return this.http.put(environment.API_EDIT_PATIENT + "/" + requestBody.patientId, requestBody);
  }

}
