import { Component, OnInit , Output, EventEmitter   } from '@angular/core';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { TransportationModel } from '../transportation-model';
import { environment } from '../../environments/environment'

@Component({
  selector: 'app-show-transportation',
  templateUrl: './show-transportation.component.html',
  styleUrls: ['./show-transportation.component.css']
})
export class ShowTransportationComponent implements OnInit {
  public items: TransportationModel[];


  public message: string;
  public isLoading: boolean;
  @Output("closePopup") closePopup = new EventEmitter();

  constructor(private tService : TreatmenthistoryService) { }

  ngOnInit() {
    let patientId = localStorage.getItem("patientId");
    this.getData(patientId);
  }
  getData(patientId: string): any {
    this.tService.getTransportationHistoryForPatient(patientId).subscribe(success =>{
      this.items = success;
      console.log(success)
    }, error => {
      console.log(environment.ERROR_SOMETHING_WENT_WRONG);
    });
  }
}
