import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgbDateStruct, NgbCalendar } from '@ng-bootstrap/ng-bootstrap';
import { ModalService } from '../modal.service';
import { AddPatientService } from './add-patient.service';
import { PatientService } from '../patient/patient.service';
import { Subscription } from 'rxjs';
import { environment } from '../../environments/environment';
import { LoginService } from '../login/login.service';
import { CommonService } from '../service/common.service';

@Component({
  selector: 'app-add-patient',
  templateUrl: './add-patient.component.html',
  styleUrls: ['./add-patient.component.css']
})
export class AddPatientComponent implements OnInit, OnDestroy {
  ngOnDestroy(): void {
    this.patientSubscription.unsubscribe();
  }
  public patientSubscription: Subscription;
  public patientModel: any;
  public username: string;
  public password: string;
  public confirmPassword: string;
  public message: string;
  public isLoading: boolean;
  public cityState: {};
  public institutionInCity : any [] = [];
  public cities : any [] = [];
  public allInstitutions : any [] = [];
  public childBelongsToInstitution : any;
  public logonGenerated = false;

  constructor(public _patientAddService: AddPatientService,
    public _patientService: PatientService,
    public _userService: CommonService,
    private loginService: LoginService,
    private modalService: ModalService) {
    this.initData();
    _userService.getAllCitiesState().subscribe( result => this.cities = result );
    _userService.getAllInstitution('').subscribe( result => this.allInstitutions = result );
  }

  initData() {
    this.patientModel = {
      patientId: null,
      institutionsFileNo: "",
      childDob: "",
      childName:"",
      dateOfBirth: {
        day: new Date().getDate(),
        month: new Date().getMonth(),
        year: new Date().getFullYear()
      },
      childBelongsToInstitution: -1,
      enteredByUserId: null,
      userId: -1,
      patientGuardianName: "",
      gender: "",
      age: "",
      primary_contact_no: "",
      email_id: "",
      address: "",
      cityState: {
        cityId: 0,
        cityName: "",
        cityState: ""
      },
      alt_con_no_1: "",
      alt_con_no_2: ""
    }
  }

  ngOnInit() {
    let selectedPatient = localStorage.getItem("selectedPatient");
    if (selectedPatient) {
      this.patientModel = JSON.parse(selectedPatient);
      this.username = this.patientModel.childName;
      let dateSplit = this.patientModel.childDob.split("-");
      this.patientModel.dateOfBirth = {
        day: parseInt(dateSplit[0]),
        month: parseInt(dateSplit[1]),
        year: parseInt(dateSplit[2])
      }
    }
    
  }

  closePatient() {
    let data = {
      isShow: false,
      component: ""
    }
    this.modalService.setModal(data);
    localStorage.setItem("selectedPatient", "");
  }

  savePatient() {
    this.message = "";
    if (!this.username) {
      return this.message = environment.USERNAME_NOT_FOUND;
    }

    if (this.username.length < 4) {
      return this.message = environment.USERNAME_VALIDATION;
    }

    if (!this.patientModel.email_id) {
      return this.message = environment.EMAIL_NOT_FOUND;
    } else if (!(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.patientModel.email_id))) {
      return this.message = environment.EMAIL_INVALID;
    }

    if (!this.password) {
      return this.message = environment.PASSWORD_NOT_FOUND;
    }
    if (!this.confirmPassword) {
      return this.message = environment.CPASSWORD_NOT_FOUND;
    }

    if (this.password.length < 6) {
      return this.message = environment.PASSWORD_VALIDATION;
    }

    if (this.password !== this.confirmPassword) {
      return this.message = environment.PP_CP_INVALID;
    }
    let data = {
      userName: this.username,
      userEmail: this.patientModel.email_id,
      userPass: this.password,
      userPhone: this.patientModel.primary_contact_no,
      roleId: [5]
    }

    this.isLoading = true;

    this._userService.addUser(data).subscribe(success => {
      console.log(success);
      this.isLoading = false;
      this.patientModel.userId = parseInt(success.message.split(":")[1].trim());
      this.savePatientInfo();
    }, error => {
      this.isLoading = false;
      console.log(error);
      this.message = error.message;
    })
  }

  savePatientInfo() {
    let data = JSON.parse(JSON.stringify(this.patientModel));
    // data.child
    data.childDob = this.patientModel.dateOfBirth.day + "-" + this.patientModel.dateOfBirth.month + "-" + this.patientModel.dateOfBirth.year;
    data.cityState = this.cityState;
    data.childName = this.username;
    data.userId = this.patientModel.userId;
    data.enteredByUserId = localStorage.getItem('userId');
    console.log(JSON.stringify(data));
    this._patientAddService.addPatient(data).subscribe(success => {
      this.message = "Patient Information Saved."
      this._patientService.triggerPatientUpdateList();
    }, error => {
      this.message = environment.ERROR_SOMETHING_WENT_WRONG;
      console.log(error);
    })
  }

  editPatient() {
    let data = JSON.parse(JSON.stringify(this.patientModel));

    data.childDob = this.patientModel.dateOfBirth.day + "-" + this.patientModel.dateOfBirth.month + "-" + this.patientModel.dateOfBirth.year;

    this._patientAddService.editPatient(data).subscribe(success => {
      console.log(success);
      this.message = "Patient Information Updated."
      this._patientService.triggerPatientUpdateList();
    }, error => {
      console.log(error);
    })
  }

}
