import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { Router } from '@angular/router';
import { environment } from '../../environments/environment';
import { LoginService } from './login.service';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public message: string;
  public error: boolean;
  public userName: string;
  public password: string;
  public cPassword: string;
  public email: string;
  public userPhone: string;
  public logonId: string;
  public slogan: string;
  public slogans: string[] = [];
  
  public modalData: any = {
    isShow: false
  }
  public bkUrl : {}

  public isLoading: boolean;
  public isRegistraion: boolean = false;
  public isChangePassword: boolean = false;
  public isLogin: boolean = false;
  public isForgotLogonId: boolean = false;
  
  

  constructor(private authService: AuthService,
    private loginService: LoginService,
    public router: Router) {
    
    this.slogans.push("--- We believe that every child has the right to a dignified life");
    this.slogans.push("--- We want to see every Cleft Child in our nation getting comprehensive care.");
    this.slogans.push("--- Will you help us gift a Smile?");
    this.changeSlogan();
    Observable.interval(6000).takeWhile(() => true).subscribe(() => this.changeSlogan());
    this.authService.setAuth({ isAuth: false });
  }

  changeSlogan(){
    this.slogan = "";
    let i = this.getRandom()-2;
    if(i < 0 ){
      i =i+1;
    }
    else if(i > this.slogans.length){
      i = this.slogans.length-1; 
    }

    this.slogan = this.slogans[i];
  }

  closeModal() {
    this.modalData.isShow = false;
  }

  openLogin() {
    this.clearData();
    this.isLogin = true;
    this.modalData.isShow = true;
  }

  ngOnInit() {
    this.bkUrl = this.getBkUrl();
    // Observable.interval(6000).takeWhile(() => true).subscribe(() => this.getBkUrl());
  }

  getRandom(){
    return Math.floor(Math.random() * Math.floor(4)) + 1 ;
  }

  getBkUrl(){
    const styles = {
      'background-image': 'url(../../assets/images/' + this.getRandom() + '.jpg)',
    };
    console.log(styles);
    return styles;
  }

  switchReg() {
    this.clearData()
    this.isRegistraion = true;
  }
  
  clearData() {
    this.isLogin = false;
    this.isChangePassword = false;
    this.isRegistraion = false;
    this.isForgotLogonId = false;
  }

  openChangePassword() {
    this.clearData();
    this.isChangePassword = true;
    this.modalData.isShow = true;
  }

  openForgotLogonId() {
    this.clearData();
    this.isForgotLogonId = true;
    this.modalData.isShow = true;
  }

  registration() {
    this.message = "";
    if (!this.userName) {
      return this.message = environment.USERNAME_NOT_FOUND;
    }

    if (this.userName.length < 4) {
      return this.message = environment.USERNAME_VALIDATION;
    }

    if (!this.email) {
      return this.message = environment.EMAIL_NOT_FOUND;
    } else if (!(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.email))) {
      return this.message = environment.EMAIL_INVALID;
    }

    if (!this.password) {
      return this.message = environment.PASSWORD_NOT_FOUND;
    }
    if (!this.cPassword) {
      return this.message = environment.CPASSWORD_NOT_FOUND;
    }

    if (this.password.length < 6) {
      return this.message = environment.PASSWORD_VALIDATION;
    }

    if (this.password !== this.cPassword) {
      return this.message = environment.PP_CP_INVALID;
    }

    if(!this.userPhone) {
      return this.message = environment.MOBILE_NOT_FOUND;
    }

    if(this.userPhone.length < 10) {
      return this.message = environment.MOBILE_INVALID;
    }

    let data = {
      userName: this.userName,
      userEmail: this.email,
      userPass: this.password,
      userPhone: this.userPhone
    }

    this.isLoading = true;

    this.loginService.registration(data).subscribe(success => {
      console.log(success);
      this.isLoading = false;
      this.isRegistraion = false;
      this.message = environment.LOGON_ID_GENERATED + success.message;
      this.openLogin();
    }, error => {
      this.isLoading = false;
      console.log(error);
      this.message = error.message;
      this.authService.setAuth({ isAuth: false, role: "", token: "" });
    })

  }

  login() {
    this.message = "";
    if (!this.logonId) {
      return this.message = environment.LOGON_ID_NOT_FOUND;
    }
    if (!this.password) {
      return this.message = environment.PASSWORD_NOT_FOUND;
    }
    let data = {
      logonId: this.logonId,
      password: this.password
    }
    this.isLoading = true;

    this.loginService.login(data).subscribe(success => {
      console.log(success);
      localStorage.setItem("token", success.accessToken)
      this.loginService.getRole({}).subscribe(success => {
        console.log(JSON.stringify(success));
        localStorage.setItem("roles", JSON.stringify(success));
        this.router.navigate(["dashboard"], { replaceUrl: true });
        this.isLoading = false;
      })
    }, error => {
      this.isLoading = false;
      console.log(error);
      localStorage.setItem("token", "")
      this.message = error.message;
      this.authService.setAuth({ isAuth: false, role: "", token: "" });
    })
  }

  changePassword() {
    this.message="";
    if (!this.logonId) {
      return this.message = environment.LOGON_ID_NOT_FOUND;
    }
    if (!this.password) {
      return this.message = environment.PASSWORD_NOT_FOUND;
    }
    if (!this.cPassword) {
      return this.message = environment.CPASSWORD_NOT_FOUND;
    }

    if (this.password.length < 6) {
      return this.message = environment.PASSWORD_VALIDATION;
    }

    if (this.password !== this.cPassword) {
      return this.message = environment.PP_CP_INVALID;
    }    

    if(!this.userPhone) {
      return this.message = environment.MOBILE_NOT_FOUND;
    }

    if(this.userPhone.length < 10) {
      return this.message = environment.MOBILE_INVALID;
    }
    let data = {
      logonId: this.logonId,
      newPassword: this.password,
      mobileNumber: this.userPhone
    }
    this.isLoading = true;
    this.loginService.forgotPassword(data).subscribe((success) => {
      console.log(success);
      this.isLoading = false;
      this.isChangePassword = false;
      this.message = environment.PASSWORD_RESET_COMPLETE;
    }, (error) => {
      this.isLoading = false;
      console.log(error);
      this.message = environment.ERROR_SOMETHING_WENT_WRONG;
      this.authService.setAuth({ isAuth: false, role: "", token: "" });
    })
  }

  forgotLogonId(){
    this.message="";
    if(!this.userPhone){
      return this.message = environment.MOBILE_INVALID;
    }

    let data = {
      mobile: this.userPhone
    }
    this.isLoading = true;

    this.loginService.forgotLogonId(data).subscribe(success => {
      console.log(success);
      this.message = environment.LOGON_ID_SENT;
      this.isForgotLogonId = false;
      this.isLoading = false;
    }, error => {
      this.isLoading = false;
      console.log(error);
      this.authService.setAuth({ isAuth: false, role: "", token: "" });
      this.message = environment.ERROR_SOMETHING_WENT_WRONG
    })
  }
}
