import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { PatientService } from './patient.service';
import { ModalService } from '../modal.service';
import { Subscription } from 'rxjs';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { CommonService } from '../service/common.service';

import { EmailModel } from '../email-model';
import { SmsModel } from '../sms-model';

import { environment } from '../../environments/environment';


@Component({
  selector: 'app-patient',
  templateUrl: './patient.component.html',
  styleUrls: ['./patient.component.css']
})
export class PatientComponent implements OnInit, OnDestroy {

  public messageToSendData: string;

  public message = "";

  ngOnDestroy(): void {
    this.patientSubscription.unsubscribe();
  }
  public modalData: any = {
    isShow: false
  }

  onSelectAllChange() {
    console.log(this.selectAllPatient);
    this.patientList.forEach(element => {
      element.isChecked = this.selectAllPatient;
    });
  }

  checkSelectLength() {
    return this.patientList.filter(e => {
      return e.isChecked
    }).length;
  }

  onSelectChange() {
    let lengthOfChecked = this.checkSelectLength();

    if (lengthOfChecked === this.patientList.length) {
      this.selectAllPatient = true;
      this.selectAll.nativeElement.indeterminate = false;
    } else if (lengthOfChecked === 0) {
      this.selectAllPatient = false;
      this.selectAll.nativeElement.indeterminate = false;
    } else {
      this.selectAll.nativeElement.indeterminate = true;
    }
  }

  @ViewChild("selectAll") selectAll;

  public selectAllPatient: boolean;

  public messageData: any = {
    isShow: false
  }

  public searchObj = {
    fromDate: "",
    toDate: "",
    searchKey: "",
    patientName: ""
  }
  public patientList: any = [];
  public patientSubscription: Subscription;
  public access: any = [];

  constructor(public _patientService: PatientService,
    private modalService: ModalService,
    private treatmentService : TreatmenthistoryService,
    private commonService : CommonService
    ) {
    this.access = JSON.parse(localStorage.getItem("access"));

  }

  ngOnInit() {
    this.getAllPatient();
    this.patientSubscription = this._patientService.patientUpdatedList().subscribe(e => {
      this.getAllPatient();
    })
  }

  getAllPatient() {
    this._patientService.getAllPatient({}).subscribe(success => {
      this.patientList = success || [];

      this.patientList.forEach(element => {
        element.isChecked = false;
      });
    })
  }

  setPatientIdInLocalStorage(patientId){
    localStorage.setItem("patientId",patientId);
  }


  sendSMS(){
    this.message="";
    let data = new SmsModel;
    data.message = this.messageToSendData;
    data.to = new Array();
    this.patientList.forEach(element => {
      if(element.isChecked === true){
        data.to.push(element.primary_contact_no)
      }
    })
    console.log(data);
    this.commonService.sendSMS(data).subscribe(result => {
      console.log("Delivered");
    })
    this.message = "Done";
  }

  sendEmail(){
    this.message = "";
    let data = new EmailModel;
    data.from = environment.EMAIL_SENDER;
    data.subject = this.messageToSendData;
    // data.patientId = new Array();
    data.users = [{}];
    this.patientList.forEach(element => {
      if(element.isChecked === true){
        let internalUserData = {
          userName : element.childName,
          userEmail : element.email_id,
          patientId : element.patientId,
          appliedRole: "",
          active: true,
        }
        console.log(JSON.stringify(data));
        console.log(JSON.stringify(internalUserData));
        data.users.push(internalUserData);
      }
    })
    
    console.log(data);
    this.commonService.sendEmail(data).subscribe(result => {
      console.log("Delivered");
    }, error => {
      console.log("Failed");
    })
    this.message = "Done";
  }


  deletePatient(patientId) {
    this._patientService.deletePatient(patientId).subscribe(success => {
      this.getAllPatient();
    })
  }

  editPatient(patient) {
    this.openAddPatient();
    localStorage.setItem("selectedPatient", JSON.stringify(patient));
  }

  searchPatient() {
    this.modalData.isShow = true;
  }

  closeModal() {
    this.modalData.isShow = false;
    this.messageData.isShow = false;
  }


  openCommand() {
    this.messageData.isShow = true;
  }

  openAddPatient() {
    let data = {
      isShow: true,
      component: "patient"
    }
    console.log(data);
    this.modalService.setModal(data);
  }


  historyPatient(patient) {
    this.setPatientIdInLocalStorage(patient.patientId);
    // console.log(JSON.stringify(patient));
    // console.log(localStorage.getItem("selectedPatient"));
    let data = {
      isShow: true,
      component: "thistory"
    }
    this.modalService.setModal(data);
    localStorage.setItem("selectedPatient", JSON.stringify(patient));
  }

}
