import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { ModalService } from '../modal.service';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { CommonService } from '../service/common.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  public authSubscription: any;
  public isAuth: boolean = false;
  public userName = "";
  public modalData: any;
  public roles: string;
  public access: any = [];
  public duplicateAccess: any = [];

  public patients_count: number = 0;
  public doctors_count: number = 0;
  public users_count: number = 0;
  public institutions_count: number = 0;
  

  constructor(private authService: AuthService,
    private modalService: ModalService,
    public router: Router,
    public commonService: CommonService
    ) {
    this.modalData = this.modalService.defaut;
    this.access = JSON.parse(localStorage.getItem("access"));

    let indexAccess = this.access.menu.indexOf('dashboard');

    commonService.countDoctors().subscribe(result => this.doctors_count = result);
    commonService.countInstitutions().subscribe(result => this.institutions_count = result);
    commonService.countPatients().subscribe(result => this.patients_count = result);
    commonService.countUsers().subscribe(result => this.users_count = result);
    
    this.duplicateAccess = JSON.parse(JSON.stringify(this.access));
    if (indexAccess !== -1) {
      this.duplicateAccess.menu.splice(indexAccess, 1);
    }
  }

  ngOnInit() {
    this.authSubscription = this.authService.getAuth().subscribe((status: any) => {
      this.isAuth = status.isAuth;
      this.userName = status.userName;
      this.access = status.access;
      let indexAccess = this.access.menu.indexOf('dashboard');
      this.duplicateAccess = JSON.parse(JSON.stringify(this.access));
      if (indexAccess !== -1) {
        this.duplicateAccess.menu.splice(indexAccess, 1);
      }
      

      console.log(status);
      this.roles = status.role && status.role.toString();
      if (!status.isAuth)
        this.router.navigate(["login"], { replaceUrl: true });
    })
  }
}
