import { Component, OnInit, OnDestroy } from '@angular/core';
import { CommonService } from '../service/common.service';
import { ModalService } from '../modal.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-doctor',
  templateUrl: './doctor.component.html',
  styleUrls: ['./doctor.component.css']
})
export class DoctorComponent implements OnInit, OnDestroy {

  ngOnDestroy(): void {
    this.updaetSubscription.unsubscribe();
  }
  public doctorList: any = [];
  public updaetSubscription: Subscription;
  public access: any = [];


  constructor(public _docService: CommonService,
    private modalService: ModalService) {
    this.access = JSON.parse(localStorage.getItem("access"));

  }

  ngOnInit() {
    this.getAllDoctor();
    this.updaetSubscription = this._docService.UpdatedList().subscribe(e => {
      this.getAllDoctor();
    })
  }

  openAddDoctor() {
    let data = {
      isShow: true,
      component: "doc"
    }
    this.modalService.setModal(data);
  }

  getAllDoctor() {
    this._docService.getAllDoctor({}).subscribe(success => {
      this.doctorList = success;
    })
  }

  deleteDoctor(doctorId) {

  }

  editDoctor(doctor) {

  }

}
