import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'datepicker'
})
export class DatepickerPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if (value) {
      let dateSplit = value.childDob.split("-");
      return {
        day: dateSplit[0],
        month: dateSplit[1],
        year: dateSplit[2]
      }
    }
    return value;
  }

}
