import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { HttpHelperService } from '../http-helper.service';
import { environment } from '../../environments/environment';

@Injectable()
export class LoginService {

  constructor(public http: HttpHelperService, public _http: Http) { }

  login(requestQuery) {
    return this.http.post(environment.API_LOGIN, requestQuery);
  }

  forgotPassword(requestQuery) {
    return this.http.post(environment.API_FORGOT_PASSWORD, requestQuery);
  }

  forgotLogonId(requestQuery) {
    return this.http.get(environment.API_FORGOT_LOGON_ID, requestQuery);
  }

  getRole(requestQuery) {
    return this.http.get(environment.API_GET_ROLE, requestQuery);
  }

  registration(requestQuery) {
    return this.http.post(environment.API_REGISTRATION, requestQuery);
  }


}
