import { Component, OnInit } from '@angular/core';
import { environment } from '../../environments/environment';
import { CommonService } from '../service/common.service';

@Component({
  selector: 'app-add-doctor',
  templateUrl: './add-doctor.component.html',
  styleUrls: ['./add-doctor.component.css']
})
export class AddDoctorComponent implements OnInit {

  public docId;
  public message;
  public isLoading;
  public allInstitutions : any [] = [];

  public docData: any = {
    docName: "",
    docPhone1: "",
    docPhone2: "",
    docCenter: -1
  }
  public emailId;
  public username;
  public password;
  public confirmPassword;

  constructor(public _userService: CommonService) {
    _userService.getAllInstitution('').subscribe( result => this.allInstitutions = result );

   }

  ngOnInit() {
  }

  closeDoc() {

  }

  saveDoc() {
    this.message = "";
    if (!this.docData.docName) {
      return this.message = environment.USERNAME_NOT_FOUND;
    }

    if (this.docData.docName.length < 4) {
      return this.message = environment.USERNAME_VALIDATION;
    }

    if (!this.emailId) {
      return this.message = environment.EMAIL_NOT_FOUND;
    } else if (!(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.emailId))) {
      return this.message = environment.EMAIL_INVALID;
    }

    if (!this.password) {
      return this.message = environment.PASSWORD_NOT_FOUND;
    }
    if (!this.confirmPassword) {
      return this.message = environment.CPASSWORD_NOT_FOUND;
    }

    if (this.password.length < 6) {
      return this.message = environment.PASSWORD_VALIDATION;
    }

    if (this.password !== this.confirmPassword) {
      return this.message = environment.PP_CP_INVALID;
    }
    let data = {
      userName: this.docData.docName,
      userEmail: this.emailId,
      userPass: this.password,
      userPhone: this.docData.docPhone1,
      roleId: [4]
    }

    this.isLoading = true;

    this._userService.addUser(data).subscribe(success => {
      console.log(success);
      let id = success.message.split(":")[1].trim();
      this.isLoading = false;
      this.saveDoctorInfo();
    }, error => {
      this.isLoading = false;
      console.log(error);
      this.message = error.message;
    })
  }

  saveDoctorInfo() {
    let data = JSON.parse(JSON.stringify(this.docData));
    
    this._userService.addDoctor(data).subscribe(success => {
      console.log(success);
      this.message = environment.DOCTOR_ADD_SUCCESS;
      this._userService.triggerUpdateList();
    }, error => {
      console.log(error);
    })
  }

  editDoc() {

  }

}
