import { Injectable } from '@angular/core';
import { HttpHelperService } from '../http-helper.service';
import { environment } from '../../environments/environment';
import { Subject } from 'rxjs';
import { Observable } from 'rxjs/Observable';
import { appInitializerFactory } from '@angular/platform-browser/src/browser/server-transition';

@Injectable()
export class CommonService {

  private subject = new Subject<any>();

  constructor(public http: HttpHelperService) { }

  getAllUser(requestQuery) {
    return this.http.get(environment.API_USER_ALL, requestQuery);
  }

  addUser(requestBody) {
    return this.http.post(environment.API_ADD_USER, requestBody);
  }

  editUser(requestBody) {
    return this.http.put(environment.API_EDIT_USER, requestBody);
  }

  deleteUser(userId) {
    return this.http.delete(environment.API_DELETE_USER + "/" + userId);
  }

  getAllInstitution(requestQuery) {
    return this.http.get(environment.API_INSTITUTION_ALL, requestQuery);
  }

  getAllCitiesState() {
    return this.http.get(environment.API_CITIES + environment.API_CITIES_BY_ALL);
  }

  getCitiesByState(name) {
    return this.http.get(environment.API_CITIES + environment.API_CITIES_BY_STATE+"/"+name);
  }

  getCitiesByCity(name) {
    return this.http.get(environment.API_CITIES + environment.API_CITIES_BY_CITY+"/"+name);
  }
  
  getCitiesById(id) {
    return this.http.get(environment.API_CITIES + environment.API_CITIES_BY_ID+"/"+id);
  }
  
  getInstitutionByCity(city) {
    return this.http.get(environment.API_INSTITUTION_BY_CITY + "/" +city);
  }

  addInstitution(requestBody) {
    return this.http.post(environment.API_ADD_INSTITUTION, requestBody);
  }

  editInstitution(requestBody) {
    return this.http.put(environment.API_EDIT_INSTITUTIONR, requestBody);
  }

  deleteInstitution(instId) {
    return this.http.delete(environment.API_DELETE_INSTITUTION + "/" + instId);
  }

  getAllDoctor(requestQuery) {
    return this.http.get(environment.API_DOCTOR_ALL, requestQuery);
  }

  addDoctor(requestBody) {
    return this.http.post(environment.API_ADD_DOCTOR, requestBody);
  }

  editDoctor(requestBody) {
    return this.http.put(environment.API_EDIT_DOCTOR, requestBody);
  }

  deleteDoctor(instId) {
    return this.http.delete(environment.API_DELETE_DOCTOR + "/" + instId);
  }
  
  countPatients(){
    return this.http.get(environment.API_EDIT_PATIENT + environment.API_COUNT);
  }
  countDoctors(){
    return this.http.get(environment.API_EDIT_DOCTOR + environment.API_COUNT);
  }
  countUsers(){
    return this.http.get(environment.API_EDIT_USER + environment.API_COUNT);
  }
  countInstitutions(){
    return this.http.get(environment.API_EDIT_INSTITUTIONR + environment.API_COUNT);
  }


  sendSMS(requestBody){
    return this.http.post(environment.API_SEND_SMS, requestBody);
  }

  sendEmail(requestBody){
    return this.http.post(environment.API_SEND_EMAIL, requestBody);
  }


  triggerUpdateList() {
    this.subject.next();
  }

  UpdatedList(): Observable<any> {
    return this.subject.asObservable();
  }

}
