import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { AuthGuard } from './auth.guard';
import { AddPatientComponent } from './add-patient/add-patient.component';
import { AddUsersComponent } from './add-users/add-users.component';
import { SearchPatientsComponent } from './search-patients/search-patients.component';
import { PatientComponent } from './patient/patient.component';
import { UsersComponent } from './users/users.component';
import { InstitutionsComponent } from './institutions/institutions.component';
import { AddDoctorComponent } from './add-doctor/add-doctor.component';
import { DoctorComponent } from './doctor/doctor.component';

//Fresh Changes
// import { UserLoginComponent } from './user-login/user-login.component';
// import { UserRegistrationComponent } from './user-registration/user-registration.component';
// import { UserResetPasswordComponent } from './user-reset-password/user-reset-password.component';
// import { UserForgotLogonIdComponent } from './user-forgot-logon-id/user-forgot-logon-id.component';




const routes: Routes = [
    { path: '', redirectTo: '/login', pathMatch: 'full' },
    
    { path: 'login', component: LoginComponent },       //Fresh Changes
    // { path: 'forgot/logonid', component: UserForgotLogonIdComponent },       //Fresh Changes
    // { path: 'register', component: UserRegistrationComponent },       //Fresh Changes
    // { path: 'forgot/password', component: UserResetPasswordComponent },       //Fresh Changes

    { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
    { path: 'patients', component: PatientComponent, canActivate: [AuthGuard] },
    { path: 'doctors', component: DoctorComponent, canActivate: [AuthGuard] },
    { path: 'users', component: UsersComponent, canActivate: [AuthGuard] },
    { path: 'institutions', component: InstitutionsComponent, canActivate: [AuthGuard] },
    { path: 'addpatient', component: AddPatientComponent, canActivate: [AuthGuard] },
    { path: 'adduser', component: AddUsersComponent, canActivate: [AuthGuard] },
    { path: 'adddoctor', component: AddDoctorComponent, canActivate: [AuthGuard] },
    { path: 'searchpatient', component: SearchPatientsComponent, canActivate: [AuthGuard] }
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: true })],
    exports: [RouterModule]
})
export class AppRouting { }