import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { InterventionModel } from '../intervention-model';
import { TreatmentHistoryModel } from '../treatment-history-model';
import { DoctorService } from '../service/doctor.service';

import { environment } from '../../environments/environment'


@Component({
  selector: 'app-add-double-intervention',
  templateUrl: './add-double-intervention.component.html',
  styleUrls: ['./add-double-intervention.component.css']
})
export class AddDoubleInterventionComponent implements OnInit {

  public dInterId: any;
  public dInterData: InterventionModel;
  public treatmentHistory: TreatmentHistoryModel;
  public patientDetails: {};

  public doctors:[];


  public message: string;
  public isLoading: boolean;
  @Output("closePopup") closePopup = new EventEmitter();

  constructor(private tService : TreatmenthistoryService,  private dService : DoctorService) { }

  ngOnInit() {
    this.dInterData = new InterventionModel;
    this.treatmentHistory = new TreatmentHistoryModel;
    this.patientDetails = this.getPatientData();
    this.getDoctors();
  }

  getPatientData(){
    return JSON.parse(localStorage.getItem("selectedPatient"));
  }

  getDoctors(){
    this.dService.getAllDoctors().subscribe(success =>{
      this.doctors = success;
    }, error => {
      console.log(environment.ERROR_SOMETHING_WENT_WRONG);
    });
  }

  dateMaker(dateObject){
    let stringDate = dateObject.day + "-" + dateObject.month + "-" + dateObject.year; 
    return stringDate;
  }

  saveDoubleInter() {
    this.message = "";
    let data = this.dInterData;
    data.admisionDt = this.dateMaker(this.dInterData.admDt);
    data.intvDt = this.dateMaker(this.dInterData.intDt);
    data.dischrgDt = this.dateMaker(this.dInterData.dschDt);
    data.intrvType = 2;
    data.enteredBy = localStorage.getItem("userId");
    data.patientId = Number(localStorage.getItem("patientId"));

    this.tService.addDoubleIntervention(data).subscribe(result => {
      this.dInterId = result;
      this.message = environment.TREATMENT_DI_ADD_SUCCESS;
      this.updateTreatmentHistory();
    });
  }

  
  updateTreatmentHistory(){
    let patientId = localStorage.getItem("patientId");
    this.tService.getTreatmentHistoryForPatient(patientId).subscribe(result => {
      this.treatmentHistory = result;
      localStorage.setItem("treatmentHistoryForSelectedPatient",JSON.stringify(this.treatmentHistory));
    }, error =>{
        console.log(environment.ERROR_SOMETHING_WENT_WRONG);
    })
  }


  editDoubleInter() {
    this.message = "";
    let data = this.dInterData;
    data.admisionDt = this.dateMaker(this.dInterData.admDt);
    data.intvDt = this.dateMaker(this.dInterData.intDt);
    data.dischrgDt  = this.dateMaker(this.dInterData.dschDt);
    data.intrvType = 1;
    data.enteredBy = localStorage.getItem("userId");
    data.patientId = Number(localStorage.getItem("patientId"));//JSON.parse(localStorage.getItem("patientId"));//this.patientDetails.patientId;
    data.intrvId = this.dInterId;

    this.tService.editSingleIntervention(data).subscribe(result => {
      this.dInterId = result;
      this.message = environment.TREATMENT_DI_EDIT_SUCCESS;
      this.updateTreatmentHistory();
    });
  }

  closeInter() {
    this.closePopup.emit("true");
  }

}
