import { Injectable } from '@angular/core';
import { HttpHelperService } from '../http-helper.service';
import { environment } from '../../environments/environment';
import { Subject } from 'rxjs';
import { Observable } from 'rxjs/Observable';

@Injectable({
  providedIn: 'root'
})
export class TreatmenthistoryService {

  private subject = new Subject<any>();

  constructor(public http: HttpHelperService) { }

  // API_TREATMENT_HISTORY_STATS
  getTreatmentHistoryStatsForPatient(patientId){
    return this.http.get(environment.API_TREATMENT_HISTORY+ environment.API_TREATMENT_HISTORY_STATS + "/" + patientId);
  }

  getTreatmentHistoryForPatient(patientId) {
    return this.http.get(environment.API_TREATMENT_HISTORY + "/" + patientId);
  }

  getSingleInterventionHistoryForPatient(patientId) {
    return this.http.get(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_SINGLE + "/" + patientId);
  }

  getDoubleInterventionHistoryForPatient(patientId) {
    return this.http.get(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_DOUBLE + "/" + patientId);
  }

  getENTHistoryForPatient(patientId) {
    return this.http.get(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ENT + "/" + patientId);
  }

  getORTHistoryForPatient(patientId) {
    return this.http.get(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ORT + "/" + patientId);
  }

  getSPTHistoryForPatient(patientId) {
    return this.http.get(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_SPT + "/" + patientId);
  }

  getTransportationHistoryForPatient(patientId) {
    return this.http.get(environment.API_TRANSPORTATION_HISTORY + environment.API_TRANSPORTATION_HISTORY_FOR_PATIENT + "/" + patientId);
  }


  addSingleIntervention(requestBody) {
    return this.http.post(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_SINGLE, requestBody);
  }

  addDoubleIntervention(requestBody) {
    return this.http.post(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_DOUBLE, requestBody);
  }

  addENT(requestBody) {
    return this.http.post(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ENT, requestBody);
  }

  addORT(requestBody) {
    return this.http.post(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ORT, requestBody);
  }
  addSPT(requestBody) {
    return this.http.post(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_SPT, requestBody);
  }
  addTransport(requestBody) {
    return this.http.post(environment.API_TRANSPORTATION_HISTORY + environment.API_TRANSPORTATION_HISTORY_ADD, requestBody);
  }


  addTreatmentHistory(requestBody) {
    return this.http.post(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ADD, requestBody);
  }

  updateTreatmentHistory(id, requestBody) {
    return this.http.put(environment.API_TREATMENT_HISTORY + "/"+ id , requestBody);
  }


  editSingleIntervention(requestBody) {
    return this.http.put(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_SINGLE, requestBody);
  }

  editDoubleIntervention(requestBody) {
    return this.http.put(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_DOUBLE, requestBody);
  }

  editENT(requestBody) {
    return this.http.put(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ENT, requestBody);
  }

  editORT(requestBody) {
    return this.http.put(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ORT, requestBody);
  }

  editSPT(requestBody) {
    return this.http.put(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_SPT, requestBody);
  }
  editTransport(transId, requestBody) {
    return this.http.put(environment.API_TRANSPORTATION_HISTORY +"/" + transId, requestBody);
  }


  deleteSingleIntervention(id) {
    return this.http.delete(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_SINGLE + "/" + id);
  }

  deleteDoubleIntervention(id) {
    return this.http.delete(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_DOUBLE + "/" +id);
  }

  deleteENT(id) {
    return this.http.delete(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ENT+"/"+id);
  }

  deleteORT(id) {
    return this.http.delete(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_ORT +"/" + id);
  }

  deleteSPT(id) {
    return this.http.delete(environment.API_TREATMENT_HISTORY + environment.API_TREATMENT_HISTORY_SPT +"/"+id);
  }
  deleteTransport(id) {
    return this.http.delete(environment.API_TRANSPORTATION_HISTORY +"/"+id);
  }
  

  // addUser(requestBody) {
  //   return this.http.post(environment.API_ADD_USER, requestBody);
  // }

  // editUser(requestBody) {
  //   return this.http.put(environment.API_EDIT_USER, requestBody);
  // }

  // deleteUser(userId) {
  //   return this.http.delete(environment.API_DELETE_USER + "/" + userId);
  // }
  triggerUpdateList() {
    this.subject.next();
  }

  UpdatedList(): Observable<any> {
    return this.subject.asObservable();
  }
}
