import { Component, OnInit , Output, EventEmitter } from '@angular/core';
import { OrtModel } from '../ort-model';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { TreatmentHistoryModel } from '../treatment-history-model';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-add-ort',
  templateUrl: './add-ort.component.html',
  styleUrls: ['./add-ort.component.css']
})
export class AddOrtComponent implements OnInit {

  public ortData : OrtModel;
  public message : string;
  public ortId : any;
  public treatmentHistory: TreatmentHistoryModel;


  public isLoading: boolean;
  @Output("closePopup") closePopup = new EventEmitter();


  constructor(private tService : TreatmenthistoryService) { 
    this.ortData = new OrtModel();
  }

  ngOnInit() {
    
  }

  dateMaker(dateObject){
    let stringDate = dateObject.day + "-" + dateObject.month + "-" + dateObject.year; 
    return stringDate;
  }

  saveortData(){
    this.message = "";
    let data = this.ortData;
    data.trtmtBookedDt = this.dateMaker(this.ortData.treatmentBookDate);
    data.trtmtStartDt = this.dateMaker(this.ortData.treatmentStartDate);
    data.addedBy = Number.parseInt(localStorage.getItem("userId"));
    data.patientId = Number.parseInt(localStorage.getItem("patientId"));

    this.tService.addORT(data).subscribe(result => {
      this.ortId = result;
      this.message = environment.TREATMENT_ORT_ADD_SUCCESS;
    });
  }

  editortData(){
    this.message = "";
    let data = this.ortData;
    data.trtmtStartDt = this.dateMaker(this.ortData.treatmentStartDate);
    data.trtmtBookedDt = this.dateMaker(this.ortData.trtmtBookedDt);
    data.addedBy = Number.parseInt(localStorage.getItem("userId"));
    data.patientId = Number.parseInt(localStorage.getItem("patientId"));
    data.orthodonticTrtmntId = this.ortId;
    this.tService.editORT(data).subscribe(result => {
      this.ortId = result;
      this.message = environment.TREATMENT_ORT_EDIT_SUCCESS;
    });
  }

  closeInter() {
    this.closePopup.emit("true");
  }

}
