import { Component, OnInit , Output, EventEmitter } from '@angular/core';
import { EntModel } from '../ent-model';
import { TreatmenthistoryService } from '../service/treatmenthistoryservice.service';
import { TreatmentHistoryModel } from '../treatment-history-model';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-add-ent',
  templateUrl: './add-ent.component.html',
  styleUrls: ['./add-ent.component.css']
})
export class AddEntComponent implements OnInit {

  public entData : EntModel;
  public message : string;
  public entId : any;
  public treatmentHistory: TreatmentHistoryModel;


  public isLoading: boolean;
  @Output("closePopup") closePopup = new EventEmitter();


  constructor(private tService : TreatmenthistoryService) { 
    this.entData = new EntModel();
  }

  ngOnInit() {
    
  }

  dateMaker(dateObject){
    let stringDate = dateObject.day + "-" + dateObject.month + "-" + dateObject.year; 
    return stringDate;
  }

  saveEntData(){
    this.message = "";
    let data = this.entData;
    data.trtmtDt = this.dateMaker(this.entData.treatmentDate);
    data.addedBy = Number.parseInt(localStorage.getItem("userId"));
    data.patientId = Number.parseInt(localStorage.getItem("patientId"));

    this.tService.addENT(data).subscribe(result => {
      this.entId = result;
      this.message = environment.TREATMENT_ENT_ADD_SUCCESS;
    });
  }

  editEntData(){
    this.message = "";
    let data = this.entData;
    data.trtmtDt = this.dateMaker(this.entData.treatmentDate);
    data.addedBy = Number.parseInt(localStorage.getItem("userId"));
    data.patientId = Number.parseInt(localStorage.getItem("patientId"));
    data.entId = this.entId;
    this.tService.editENT(data).subscribe(result => {
      this.entId = result;
      this.message = environment.TREATMENT_ENT_EDIT_SUCCESS;
    });
  }

  closeInter() {
    this.closePopup.emit("true");
  }

}
