import { Component, OnInit } from '@angular/core';
import { CommonService } from '../service/common.service';
import { ModalService } from '../modal.service';

@Component({
  selector: 'app-add-institution',
  templateUrl: './add-institution.component.html',
  styleUrls: ['./add-institution.component.css']
})
export class AddInstitutionComponent implements OnInit {

  public instId: any;
  public institutionName;
  public cityState = {

  };
  public cities = [];
  public institutionAddress;
  public phone;
  public pointOfContact;
  public message;
  public isLoading;

  constructor(public _instService: CommonService,
    private modalService: ModalService) { 
      _instService.getAllCitiesState().subscribe( result => this.cities = result );

    }

  ngOnInit() {
    let selectedInst = localStorage.getItem("selectedInst");
    if (selectedInst) {
      
      let instSelected = JSON.parse(selectedInst);
      this.institutionName = instSelected.institutionName;
      this.cityState = instSelected.city;
      this.institutionAddress = instSelected.institutionAddress;
      this.phone = instSelected.phone;
      this.pointOfContact = instSelected.pointOfContact;
      this.message = instSelected.message;
    }
  }

  closeInst() {
    let data = {
      isShow: false,
      component: ""
    }
    this.modalService.setModal(data);
    localStorage.setItem("selectedInst", "");
  }

  editInst() {

  }
  saveInst() {
    let data = {
      institutionName: this.institutionName,
      cityState: this.cityState,
      institutionAddress: this.institutionAddress,
      phone: this.phone,
      pointOfContact: this.pointOfContact
    }
    this._instService.addInstitution(data).subscribe(success => {
      this.message = "Added Institution";
      this._instService.triggerUpdateList();
    }, error => {
      this.message = error.message;
    })
  }

}
