import { Component, OnInit } from '@angular/core';
import { CommonService } from '../service/common.service';
import { ModalService } from '../modal.service';
import { environment } from '../../environments/environment';

@Component({
  selector: 'app-add-users',
  templateUrl: './add-users.component.html',
  styleUrls: ['./add-users.component.css']
})
export class AddUsersComponent implements OnInit {

  public username: string;
  public email: string;
  public password: string;
  public user_phone: string;
  public confirmPassword: string;
  public userId: any;
  public isLoading: boolean;
  public message: string;
  public roleId: any = [1];
  public roleList: any = JSON.parse(localStorage.getItem("roles"));

  constructor(public _userService: CommonService,
    private modalService: ModalService) { }

  ngOnInit() {
  }

  updateRole(roleId, e) {
    if (e.target.checked && this.roleId.indexOf(roleId) === -1) {
      this.roleId.push(roleId)
    } else if (this.roleId.indexOf(roleId) !== -1) {
      let removeIndex = this.roleId.indexOf(roleId);
      this.roleId.splice(removeIndex, 1);
    }
  }

  saveUser() {
    this.message = "";
    if (!this.username) {
      return this.message = environment.USERNAME_NOT_FOUND;
    }

    if (this.username.length < 4) {
      return this.message = environment.USERNAME_VALIDATION;
    }

    if (!this.email) {
      return this.message = environment.EMAIL_NOT_FOUND;
    } else if (!(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.email))) {
      return this.message = environment.EMAIL_INVALID;
    }

    if (!this.password) {
      return this.message = environment.PASSWORD_NOT_FOUND;
    }
    if (!this.confirmPassword) {
      return this.message = environment.CPASSWORD_NOT_FOUND;
    }

    if (this.password.length < 6) {
      return this.message = environment.PASSWORD_VALIDATION;
    }

    if (this.password !== this.confirmPassword) {
      return this.message = environment.PP_CP_INVALID;
    }

    if (this.roleId.length === 0) {
      return this.message = environment.ROLE_INVALID;
    }

    let data = {
      userName: this.username,
      userEmail: this.email,
      userPass: this.password,
      roleId: this.roleId,
      user_phone: this.user_phone
    }
    this._userService.addUser(data).subscribe(success => {
      console.log(success);
      this._userService.triggerUpdateList();
    }, error => {
      this.message = error.message;
    })
  }

  editUser() {
    this.message = "";
    if (!this.username) {
      return this.message = environment.USERNAME_NOT_FOUND;
    }

    if (this.username.length < 4) {
      return this.message = environment.USERNAME_VALIDATION;
    }

    if (!this.email) {
      return this.message = environment.EMAIL_NOT_FOUND;
    } else if (!(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.email))) {
      return this.message = environment.EMAIL_INVALID;
    }

    if (!this.password) {
      return this.message = environment.PASSWORD_NOT_FOUND;
    }
    if (!this.confirmPassword) {
      return this.message = environment.CPASSWORD_NOT_FOUND;
    }

    if (this.password.length < 6) {
      return this.message = environment.PASSWORD_VALIDATION;
    }

    if (this.password !== this.confirmPassword) {
      return this.message = environment.PP_CP_INVALID;
    }

    if (this.roleId.length === 0) {
      return this.message = environment.ROLE_INVALID;
    }

    let data = {
      userName: this.username,
      userEmail: this.email,
      userPass: this.password,
      roleId: this.roleId,
      user_phone: this.user_phone
    }
    this._userService.addUser(data).subscribe(success => {
      console.log(success);
      this._userService.triggerUpdateList();
    }, error => {
      this.message = error.message;
    })
  }

  closeUser() {
    let data = {
      isShow: false,
      component: ""
    }
    this.modalService.setModal(data);
    localStorage.setItem("selectedUser", "");
  }

}
