import { Injectable } from '@angular/core';
import { HttpHelperService } from '../http-helper.service';
import { environment } from '../../environments/environment';
import { Subject } from 'rxjs';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class PatientService {

  private subject = new Subject<any>();
  private selectedPatientSub = new Subject<any>();
  constructor(public http: HttpHelperService) { }

  getAllPatient(requestQuery) {
    return this.http.get(environment.API_PATIENT_ALL, requestQuery);
  }

  deletePatient(patientId) {
    return this.http.delete(environment.API_DELETE_PATIENT + "/" + patientId);
  }

  triggerPatientUpdateList() {
    this.subject.next();
  }

  patientUpdatedList(): Observable<any> {
    return this.subject.asObservable();
  }

  triggerSelectedPatient(patient) {
    this.selectedPatientSub.next(patient);
  }

  selectedPatient(): Observable<any> {
    return this.selectedPatientSub.asObservable();
  }
}
