import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { AuthService } from './auth.service';

@Injectable()
export class AuthGuard implements CanActivate {
  constructor(private authService: AuthService) {}
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    let componentName: any = next.component;
    if (componentName.name !== "LoginComponent" && localStorage.getItem("token")) {
      var base64Url = localStorage.getItem("token").split('.')[1];
      var base64 = base64Url.replace('-', '+').replace('_', '/');
      let userData = JSON.parse(window.atob(base64));
      let authority = JSON.stringify(userData.userRole);
      localStorage.setItem("token", localStorage.getItem("token"))
      localStorage.setItem("authority",JSON.stringify(authority));
      localStorage.setItem("userId",userData.sub);
      let role = [];
      let access = {
        isEdit: false,
        isDelete: false,
        isAdd: false,
        isView: false,
        menu: []
      }
      if (authority.indexOf("ROLE_ADMIN") !== -1) {   //includes("ROLE_ADMIN")){
        role.push("Admin");
        access.isEdit = true;
        access.isDelete = true;
        access.isAdd = true;
        access.isView = true;
        access.menu = ['dashboard', 'patients', 'doctors', 'users', 'institutions']
      }
      if (authority.indexOf("ROLE_CENTER_HEAD") !== -1) {
        role.push("Center Head")
        access.isEdit = true;
        access.isDelete = true;
        access.isAdd = true;
        access.isView = true;
        access.menu = ['dashboard', 'patients', 'doctors', 'institutions']
      }
      if (authority.indexOf("ROLE_PROJECT_ANIMATOR") !== -1) {
        role.push("Project Animator")
        access.isEdit = true;
        access.isDelete = true;
        access.isAdd = true;
        access.isView = true;
        access.menu = ['dashboard', 'patients', 'doctors']
      }
      if (authority.indexOf("ROLE_DOCTOR") !== -1) {
        role.push("Doctor")
        access.isEdit = false;
        access.isDelete = false;
        access.isAdd = false;
        access.isView = true;
        access.menu = ['patients', 'doctors']
      }
      if (authority.indexOf("ROLE_USER") !== -1) {
        access.isEdit = false;
        access.isDelete = false;
        access.isAdd = false;
        access.isView = true;
        access.menu = ['patients']
        role.push("User")
      }

      localStorage.setItem("access", JSON.stringify(access));

      if (userData) {
        this.authService.setAuth({ isAuth: true, role: role, token: localStorage.getItem("token"), access: access, userName: userData.userName, userId: userData.sub });
      } else {
        this.authService.setAuth({ isAuth: false });
        return false;
      }
    } else {
      this.authService.setAuth({ isAuth: false });
      return false;
    }

    return true;
  }
}