import { Component, OnInit, OnDestroy } from '@angular/core';
import { CommonService } from '../service/common.service';
import { ModalService } from '../modal.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit, OnDestroy {

  ngOnDestroy(): void {
    this.updaetSubscription.unsubscribe();
  }
  public userList: any = [];
  public updaetSubscription: Subscription;

  constructor(public _userService: CommonService,
    private modalService: ModalService) { }

  ngOnInit() {
    this.getAllUser();
    this.updaetSubscription = this._userService.UpdatedList().subscribe(e => {
      this.getAllUser();
    })
  }

  getAllUser() {
    this._userService.getAllUser({}).subscribe(success => {
      this.userList = success;
    })
  }

  openAddUser() {
    let data = {
      isShow: true,
      component: "user"
    }
    this.modalService.setModal(data);

  }

  deleteUser(userId) {
    this._userService.deleteUser(userId).subscribe(success => {
      this.getAllUser();
    })
  }

  editUser(user) {

  }

}
