import { Component, OnInit, OnDestroy } from '@angular/core';
import { CommonService } from '../service/common.service';
import { ModalService } from '../modal.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-institutions',
  templateUrl: './institutions.component.html',
  styleUrls: ['./institutions.component.css']
})
export class InstitutionsComponent implements OnInit, OnDestroy {

  ngOnDestroy(): void {
    this.updaetSubscription.unsubscribe();
  }
  public institutionList: any;
  public updaetSubscription: Subscription;
  public access: any = [];

  constructor(public _instService: CommonService,
    private modalService: ModalService) {
    this.access = JSON.parse(localStorage.getItem("access"));
  }

  ngOnInit() {
    this.getAllInstitution();
    this.updaetSubscription = this._instService.UpdatedList().subscribe(e => {
      this.getAllInstitution();
    })
  }

  getAllInstitution() {
    this._instService.getAllInstitution({}).subscribe(success => {
      this.institutionList = success;
    })
  }

  openAddInstitution() {
    let data = {
      isShow: true,
      component: "inst"
    }
    this.modalService.setModal(data);

  }

  deleteInstitution(instiId) {
    this._instService.deleteInstitution(instiId).subscribe(success => {
      this.getAllInstitution();
    })
  }

  editInstitution(insti) {
    this.openAddInstitution();
    localStorage.setItem("selectedInst", JSON.stringify(insti));
  }

}
