import { Injectable } from '@angular/core';
import { Http, RequestOptions, ResponseContentType, Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map.js';
import 'rxjs/add/operator/catch.js';
import { environment } from '../environments/environment';

@Injectable()
export class HttpHelperService {

  constructor(private http: Http) { }

  generateHeader(reqParam) {
    if (localStorage.getItem("token")) {
      let headers = new Headers({
        'Authorization': "Bearer " + localStorage.getItem("token")
      })
      return new RequestOptions({ headers: headers, params: reqParam });
    } else {
      return new RequestOptions({ params: reqParam });
    }
  }

  get(url, reqParam?, isLoading?) {
    // reqParam["v"] = new Date().getTime();
    const options = this.generateHeader(reqParam);
    return this.http.get(environment.BASE_URL + url, options)
      .map(this.successHandler)
      .catch(this.errorHandler);
  }

  post(url, reaBodyparam?, reqParam?) {
    const options = this.generateHeader(reqParam);
    return this.http.post(environment.BASE_URL + url, reaBodyparam, options)
      .map(this.successHandler)
      .catch(this.errorHandler);
  }

  delete(url, reqParam?) {
    const options = this.generateHeader(reqParam);
    return this.http.delete(environment.BASE_URL + url, options)
      .map(this.successHandler)
      .catch(this.errorHandler);
  }

  put(url, reaBodyparam?, reqParam?) {
    const options = this.generateHeader(reqParam);
    return this.http.put(environment.BASE_URL + url, reaBodyparam, options)
      .map(this.successHandler)
      .catch(this.errorHandler);
  }

  successHandler = (res) => {
    let result = res.json();
    return result;
  }

  errorHandler = (e) => {
    return Observable.throw(e.json());
  }


}
